<template>
  <div class="profile full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll" class="scroll-view" v-if="!isInit">
        <div>
          <div class="profile-avatar-wrapper">
            <div class="profile-avatar-panel">
                <img class="profile-avatar" :src="info.avatar" @error="defaultImage">
            </div>
          </div>
          <div class="profile-title">基本资料</div>
          <div class="profile-item-wrapper">
            <div class="profile-item">
              <span class="profile-item-name">姓名</span>
              <span class="profile-item-value">{{ info.username }}</span>
            </div>
            <div class="profile-item">
              <span class="profile-item-name">{{ auth.type === 'tz' ? '机构' : '公司' }}</span>
              <span class="profile-item-value">{{ info.company }}</span>
            </div>
            <div class="profile-item">
              <span class="profile-item-name">职位</span>
              <span class="profile-item-value">{{ info.position }}</span>
            </div>
            <div class="profile-item">
              <span class="profile-item-name">手机</span>
              <span class="profile-item-value">{{ info.phone }}</span>
            </div>
            <div class="profile-item" v-if="auth && auth.type !== 'syk'">
              <span class="profile-item-name">邮箱</span>
              <span class="profile-item-value">{{ info.email }}</span>
            </div>
            <div class="profile-item" v-if="auth && auth.type !== 'syk'">
              <span class="profile-item-name">微信</span>
              <span class="profile-item-value">{{ info.weixin }}</span>
            </div>
          </div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-toast ref="toast" :text="toastText"></pem-toast>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import { mapState } from 'vuex'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle } from 'common/js/util'

// 临时 后期要删除
import tempAvatarUrl from 'common/image/icon-default-avatar@2x.png'

export default {
  components: {
    PemOperationPanel,
    PemScrollView,
    PemToast
  },
  data () {
    return {
      toastText: '',
      isInit: true,
      info: {},
      cases: [],
      records: []
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-个人资料')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  computed: mapState({auth: state => state.auth}),
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getInfo()
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    toast (text) {
      this.toastText = text
      this.$refs.toast.show()
    },
    getInfo () {
      const promises = []
      promises.push(this._getMyInfomsgNew())
      // if (this.auth.type === 'tz') {
      //   promises.push(this._getEventList())
      //   promises.push(this._getWorkerList())
      // }
      Promise
      .all(promises)
      .then((results) => {
        if (+results[0].resultcode === 1) {
          this.isInit = false
          this.info = {
            username: results[0].name,
            avatar: ImgPath + results[0].logo,
            company: results[0].corpname,
            position: results[0].position,
            phone: results[0].tel,
            email: results[0].email,
            weixin: results[0].weixinid
          }
          // if (results[1] && results[1].resultList && results[1].resultList.length > 0) {
          //   this.cases = results[1].resultList.map(item => {
          //     return {
          //       title: item.productname || '',
          //       company: item.companyname || '',
          //       round: item.turn || '',
          //       price: item.currency && item.amount ? (item.currency + item.amount + '万') : '',
          //       time: dateFormat(item.investtime, 'yyyy-MM')
          //     }
          //   })
          // }
          // if (results[2] && results[2].resultList && results[2].resultList.length > 0) {
          //   this.records = results[2].resultList.map(item => {
          //     return {
          //       company: item.orgname || '',
          //       position: item.positionname || '',
          //       startAt: dateFormat(item.starttime, 'yyyy-MM'),
          //       endAt: dateFormat(item.endtime, 'yyyy-MM')
          //     }
          //   })
          // }
        } else {
          this.toast('获取信息失败，请重新访问')
        }
      })
    },
    _getMyInfomsgNew () {
      return MiniSiteApi.getMyInfomsgNew()
    },
    _getEventList () {
      return MiniSiteApi.eventList()
    },
    _getWorkerList () {
      return MiniSiteApi.workerList()
    },
    defaultImage (e) {
      e.target.src = tempAvatarUrl
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
    .profile-avatar-wrapper
      padding: 0.34rem 0.3rem
      .profile-avatar-panel
        padding: 0.44rem
        text-align: center
        border: 0.02rem solid #E9E9E9;
        border-radius: 0.08rem
        .profile-avatar
          width: 1.16rem
          height: 1.16rem
          border-radius: 100%
    .profile-title
      padding: 0 0 0.12rem 0.3rem 
      font-size: 0.4rem 
      font-weight: bold
      color: #41403E
    .profile-item-wrapper
      padding: 0 0.3rem
      .profile-item
        font-size: 0
        line-bottom()
        height: 0.84rem
        &:last-of-type::after
          display: none
        span
          height: 0.84rem
          line-height: 0.84rem
          display: inline-block
          vertical-align: middle
          color: #202020
        .profile-item-name
          font-size: 0.32rem
          margin-right: 0.6rem
        .profile-item-value
          font-size: 0.28rem
</style>



